.ldBar {
  position: absolute;
  max-width: 45px;
  max-height: 45px;
  // background-color: rgba(0, 0, 0, 0.542);
  // box-shadow: inset 0px 0px 17px 3px rgb(0, 0, 0);
  // box-shadow: 0px 0px 17px 3px rgba(0,0,0,1);
  // border-radius: 50%;
}
.ldBar.label-center > .ldBar-label {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 0 0 3px rgb(255, 204, 0);
}
.ldBar-label:after {
  content: '%';
  display: inline;
}
.ldBar.no-percent .ldBar-label:after {
  content: '';
}

.ldBar-label {
  position: absolute;
  transform: translate(-50%, -50%);
  color: rgb(255, 255, 255);
  font-family: 'varela round';
  font-size: 1em;
  font-weight: 900;
  text-shadow: 0 0 3px rgb(0, 0, 0);
}
.ldBar path.mainline {
  /* styling of bar omitted */
}
//
.ldBar-label:after {
  content: ''; /* change from % to USD */
  font-size: 0.6em;
}
//value
.ldBar-label span {
  font-size: 0.6em;
  color: rgb(255, 255, 255);
}
